import axios from "axios";
import { useQuery } from "react-query";

export function getNews() {
  return axios.get(
    "https://newsapi.org/v2/everything?q=tesla&from=2024-06-19&sortBy=publishedAt&apiKey=344dd72d031a4da587e6daaac1861628"
  );
}





export function useData(key, fn) {
  return useQuery(key, fn, {
    select:(data)=> data.data
    // Options for react-query can be added here if needed
    // e.g., refetchOnWindowFocus: false
  });
}


