import React from "react";
import "./News.css";
import { getNews, getNews2, useData, useData1 } from "../../Hooks/useData";
import Loading from "./../Loading/Loading";
import img from "../../assets/banner (1).png";

export default function News() {
  let { data, isError, isLoading, error } = useData("news", getNews);


  if (isLoading ) {
    return <Loading></Loading>;
  }
  if (isError) {
    return <h1 className="text-center text-danger">{error.message}</h1>;
  }


  console.log(data);

  return (
    <div className="news">
      <div className="container">
        <div className="row g-4">
          {data?.articles.map((ele) => (
            <div className="col-md-3">
              <img src= {ele.urlToImage == null ? img :ele.urlToImage }  alt="" />
              <h4>{ele.author?ele.author.split(' ').slice(0,3).join(' '):"Amar Reda"}</h4>
              <p>
                {ele.title.split(" ").slice(0, 8).join(" ")}
                <a href={ele.url} target="_blank">
                  {" "}
                  More...
                </a>
              </p>
            </div>
          ))}
          
        </div>
      </div>
    </div>
  );
}
